import axios from 'axios';

const API_URL = 'http://localhost:9010/book/';

export default {
  getAllBooks() {
    return axios.get('http://localhost:9010/book/all');
  },

  getBookById(bookId) {
    return axios.get(`${API_URL}${bookId}`);
  },

  createBook(book) {
    return axios.post('http://localhost:9010/book/create', book);
  },

  updateBook(bookId, book) {
    return axios.put(`${API_URL}${bookId}`, book);
  },

  deleteBook(bookId) {
    return axios.delete(`${API_URL}${bookId}`);
  }
};
